.page {
  font-family: "Poppins", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 140%;
  color: $colorWhite;

  background-color: $colorDark;

  scroll-behavior: smooth;

  &__body {
    &--with-menu {
      overflow: hidden;
    }
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transform: translateX(100%);
    transition: transform 0.5s;

    &:target {
      transform: translateX(0);
    }
  }

  &__section {
    margin-bottom: 60px;

    @include onTablet {
      margin-bottom: 120px;
    }

    &:nth-of-type(2) {
      margin-bottom: 92px;

      @include onTablet {
        margin-bottom: 120px;
      }

      @include onDesktop {
        margin-bottom: 144px;
      }
    }

    &:nth-of-type(3) {
      margin-bottom: 80px;

      @include onTablet {
        margin-bottom: 120px;
      }

      @include onDesktop {
        margin-bottom: 180px;
      }
    }

    &:nth-of-type(4) {
      margin-bottom: 120px;

      @include onDesktop {
        margin-bottom: 182px;
      }
    }
  }

  &__section-title {
    margin-bottom: 48px;

    font-weight: 500;
    font-size: 32px;
    line-height: 32px;
    text-align: center;
    letter-spacing: -1.5px;
    color: $colorWhite;

    @include onTablet {
      font-size: 48px;
      line-height: 48px;
    }

    @include onDesktop {
      font-size: 64px;
      line-height: 64px;
    }
  }
}
